html {
  line-height: 1.15;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

body {
  margin: 0;
}

article,
aside,
footer,
header,
nav,
section {
  display: block;
}

h1 {
  font-size: 2em;
  margin: 0.67em 0;
}

figcaption,
figure,
main {
  display: block;
}

figure {
  margin: 1em 40px;
}

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}

pre {
  font-family: monospace, monospace;
  font-size: 1em;
}

a {
  background-color: transparent;
  -webkit-text-decoration-skip: objects;
}

abbr[title] {
  border-bottom: none;
  text-decoration: underline;
  text-decoration: underline dotted;
}

b,
strong {
  font-weight: inherit;
}

b,
strong {
  font-weight: bolder;
}

code,
kbd,
samp {
  font-family: monospace, monospace;
  font-size: 1em;
}

dfn {
  font-style: italic;
}

mark {
  background-color: #ff0;
  color: #000;
}

small {
  font-size: 80%;
}

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

audio,
video {
  display: inline-block;
}

audio:not([controls]) {
  display: none;
  height: 0;
}

img {
  border-style: none;
}

svg:not(:root) {
  overflow: hidden;
}

button,
input,
optgroup,
select,
textarea {
  font-family: sans-serif;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
}

button,
input {
  overflow: visible;
}

button,
select {
  text-transform: none;
}

button,
html [type="button"],
[type="reset"],
[type="submit"] {
  -webkit-appearance: button;
}

button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

button:-moz-focusring,
[type="button"]:-moz-focusring,
[type="reset"]:-moz-focusring,
[type="submit"]:-moz-focusring {
  outline: 1px dotted ButtonText;
}

fieldset {
  padding: 0.35em 0.75em 0.625em;
}

legend {
  box-sizing: border-box;
  color: inherit;
  display: table;
  max-width: 100%;
  padding: 0;
  white-space: normal;
}

progress {
  display: inline-block;
  vertical-align: baseline;
}

textarea {
  overflow: auto;
}

[type="checkbox"],
[type="radio"] {
  box-sizing: border-box;
  padding: 0;
}

[type="number"]::-webkit-inner-spin-button,
[type="number"]::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

[type="search"]::-webkit-search-cancel-button,
[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

details,
menu {
  display: block;
}

summary {
  display: list-item;
}

canvas {
  display: inline-block;
}

template {
  display: none;
}

[hidden] {
  display: none;
}

.clearfix:after {
  visibility: hidden;
  display: block;
  font-size: 0;
  content: "";
  clear: both;
  height: 0;
}

html {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

/* === HEADER COMPONENT === */

header {
  background: #e27a05;
  color: whitesmoke;
}

.header {
  align-items: center;
  display: flex;
  justify-content: space-between;
  max-width: 1400px;
  margin: 0 auto;
  padding: 0px;
  width: 90%;
}

header a {
  color: whitesmoke;
}

/* === CREATE POLL COMPONENT === */

.createPollForm {
  background: #f5f5f5;
  border: 7px solid #2d0c30;
  border-radius: 15px;
  box-shadow: 0 0 15px rgba(45, 12, 48, 0.3);
  display: flex;
  flex-flow: row wrap;
  margin: 20px auto;
  max-width: 800px;
  padding: 30px;
  width: 90%;
}

.createPollForm:focus-within {
  box-shadow: 0 0 25px rgba(45, 12, 48, 0.3);
}

.createPollForm label span {
  text-transform: capitalize;
  padding: 10px;
  font-family: "Montserrat", sans-serif;
}

.createPoll h2,
.createPoll p {
  padding: 10px;
  /* margin: 15px 0; */
  max-width: 800px;
}

.createPollForm label {
  font-family: "Barlow", sans-serif;
  font-size: 1.6rem;
  padding: 10px;
  /* margin: 10px 10px; */
}

.createPollForm input,
.createPollForm span {
  margin: 15px 0;
  font-size: 1.6rem;
  width: 90%;
}

.createPollForm input {
  border: 3px solid #2d0c30;
  border-radius: 10px;
  font-size: 2.5rem;
  margin: 10px auto;
  padding: 10px;
  background-color: #eae100;
  font-family: "Barlow", sans-serif;
}

.buttonBox {
  width: 80%;
  display: flex;
  justify-content: space-between;
  justify-items: center;
  align-items: center;
  position: relative;
}

.createPollForm button {
  margin: 3% 5%;
  padding: 10px;
  padding-bottom: 10px;
  font-family: "Barlow", sans-serif;
  font-weight: 500;
  font-size: 1.6rem;
  display: flex;
  flex-wrap: wrap;
  flex: 1 0 200px;
}

.createPollForm .resetButton {
  background-color: lightgrey;
  color: grey;
}

input:focus,
input:focus-within {
  border: 6px solid #e27a05;
  border-radius: 10px;
  outline: none;
}

/*******************************/

/* Portrait tablet and small desktops */

@media (max-width: 1000px) {
  .createPollForm,
  .createPoll p,
  .createPoll h2 {
    padding: 10px;
    margin: 10px 0;
    max-width: 800px;
  }
  .createPollForm label {
    margin: 10px 0;
  }
}

/* Landscape phone to portrait tablet */

@media (max-width: 720px) {
  header h1 {
    font-size: 35px;
  }
  .createPollForm input {
    font-size: 1.8rem;
    /* margin: 10px 10px; */
  }
  .createPollForm label {
    /* margin: 10px 10px; */
  }
  .createPollForm h2 {
    font-size: 2rem;
  }
  .createPollForm button {
    padding: 10px;
    padding-bottom: 10px;
    flex: 1 0 100px;
  }
}

/* Landscape phones and down */

@media (max-width: 320px) {
  header h1 {
    text-align: center;
  }
  .createPollForm input {
    font-size: 1.8rem;
    margin: 10px 10px;
  }
  .createPollForm h2 {
    font-size: 2rem;
    text-align: center;
    width: 80%;
  }
  .createPollForm label {
    margin: 10px 10px;
  }
}
